const Wrapper = styled.section`
    background-color: #03031B;
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: white;
    font-family: Open Sans;
    padding: 80px 20px;
    .hexado-lvl-container{
        padding-top: 80px;
        @media screen and (max-width:1240px) {
            grid-template-columns: repeat(3, minmax(0, 1fr));
        }
        @media screen and (max-width:890px) {
            grid-template-columns: repeat(2, minmax(0, 1fr));
        }
        @media screen and (max-width:580px) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
    .img-wrapper {
        height: fit-content;
    }
    .hexado-videogame-title{
        max-width: 980px;
    }
    .hexado-lvl-card {
        max-width: 390px;
    }
    .lvl-mark {
        border-radius: 100%;
        transform: translateY(-50px);
        color: #FFC300;
        div{
            padding: 8px 18px;
            border-radius: 100%;
        }
    }
    .two-place-number{
        div{
            padding: 8px 12px;
        }
    }
`
const LvlCardWrapper = styled.div`
    background-color: #101126;
    height: 100%;
    display: grid;
    grid-template-rows: auto;
`

const LvlContentWrapper = styled.div`
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 280px;
`
