import { Modal } from "@nextui-org/react";
import { SharedImage } from "constants/image-constants";
import { DevBox } from "./box";
import HEXADOImage from "./image";
import { Heading, Row, Text, Column } from "./layout";
import { Button } from "./button";
import styled from "styled-components";

const { HEXADO_BG } = SharedImage;

const VideoStyle = {
    minWidth: "1050px"
}

const SubHeading = {
    fontSize: "18px"
}

const DevBoxStyle = {
    padding: "20px",
    height: "100%"
}

const WrapperStyle = {
    paddingTop: "30px"
}

const MiniWrapperStyle = {
    paddingTop: "15px"
}

export const GameModal = ({ visible, onClose, modalData }) => {
    return (
        <Modal
            scroll
            width="1450px"
            aria-labelledby="modal-title"
            aria-describedby="modal-description"
            open={visible}
            blur
            onClose={onClose}
            style={{
                backgroundColor: "#03031B"
            }}
        >
            <Modal.Header>

            </Modal.Header>
            <Modal.Body>
                <ModalContent>
                    <HEXADOImage alignItems="flex-start" className="hexado-video" style={VideoStyle} src={HEXADO_BG} />
                    <DevBox className="dev-box" style={{ maxWidth: "400px" }}>
                        <BoxWrapper style={DevBoxStyle}>
                            <Heading>{modalData.Name}</Heading>
                            <Column style={WrapperStyle}>
                                <Heading style={SubHeading}>About</Heading>
                                <Text style={MiniWrapperStyle}>
                                {modalData.About}
                                </Text>
                            </Column>
                            <Column style={WrapperStyle}>
                                <Heading style={SubHeading}>Goal</Heading>
                                <Text style={MiniWrapperStyle}>
                                {modalData.Goal}
                                </Text>
                            </Column>
                            <Row style={WrapperStyle} justifyContent="space-between">
                                <Heading style={SubHeading}>Size</Heading>
                                <Text>{modalData.Size}</Text>
                            </Row>
                            <Row style={MiniWrapperStyle} justifyContent="space-between">
                                <Heading style={SubHeading}>Type</Heading>
                                <Text>{modalData.Type}</Text>
                            </Row>
                            <Row className="button-wrapper">
                                <Button onClick={onClose}>
                                    Back to List
                                </Button>
                            </Row>
                        </BoxWrapper>
                    </DevBox>
                </ModalContent>
            </Modal.Body>
            <Modal.Footer>

            </Modal.Footer>
        </Modal>
    )
}

const BoxWrapper = styled.div`
    background-color: #101126;
    color: white;
    .button-wrapper {
        padding-top: 50px;
    }
`

const ModalContent = styled.div`
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 30px;
    .hexado-video {
        @media screen and (max-width:1580px) {
            min-width: 0px !important;
            max-width: 100%;
        }
    }
    @media screen and (max-width:760px) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .dev-box {
        @media screen and (max-width:760px) {
            max-width: 100% !important;
        }
    }
`